.header
{
    display: flex;
    justify-content: space-between;
    margin: 10px;
    border: 1px solid black;
}

.nav-items > ul
{
    list-style-type: none;
    display: flex;
}

.nav-items > ul > li {
    padding: 10px;
    
}

.logo
{
    height: 100px;
    width: 200px;
    margin-bottom: 0px;
    margin-top: 4px;
}

.card
{
    width: 200px;
    border: 1px solid black;
    padding: 10px;
}

.card > img
{
    width: 100%;
}

.restrauntCard
{
    display: flex;
    margin: 2px;
    
}